.page {
  font-family: Poppins, sans-serif;
  font-size: 16px;
  line-height: 140%;
  color: #fff;
  scroll-behavior: smooth;
  background-color: #1d1d1d;

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    opacity: 0;
    transition: all 0.3s;
    transform: translateX(-100%);
    &:target {
      opacity: 1;
      pointer-events: all;
      transform: translateX(0);
    }
  }
}

.page:has(.page__menu:target) {
  overflow: hidden;
}
